import React from "react";
import { Link } from "react-router-dom";
import { compose, withHandlers } from "recompose";
import { withState } from "../../shared/containers/withState";
import { FormGroup } from "../../shared/components/FormGroup";
import { Input } from "../../shared/components/Input";
// import { Button } from "../../shared/components/Button";
import { withRegister } from "../../api/auth/withRegister";
import { storeAuthToken } from "../../shared/utils/localStorageUtil";
import "../../styles/routes/Auth.css";
import { AuthContext } from "../../shared/containers/withAuth";
import { placeholder } from "../../shared/constants/authConstants";

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  CardImg,
  CardTitle,
  Label,
  // FormGroup,
  Form,
  // Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col,
} from "reactstrap";

const initialState = {
  username: "",
  email: "",
  password: "",
  isSubmitting: false,
  errors: {},
};

const RegisterComponent = (props) => {
  const { setState, state } = props;
  const { username, email, password } = state;

  const [squares1to6, setSquares1to6] = React.useState("");
  const [squares7and8, setSquares7and8] = React.useState("");
  const [fullNameFocus, setFullNameFocus] = React.useState(false);
  const [emailFocus, setEmailFocus] = React.useState(false);
  const [passwordFocus, setPasswordFocus] = React.useState(false);

  React.useEffect(() => {
    document.body.classList.toggle("register-page");
    document.documentElement.addEventListener("mousemove", followCursor);
    // Specify how to clean up after this effect:
    return function cleanup() {
      document.body.classList.toggle("register-page");
      document.documentElement.removeEventListener("mousemove", followCursor);
    };
  }, []);

  const followCursor = (event) => {
    let posX = event.clientX - window.innerWidth / 2;
    let posY = event.clientY - window.innerWidth / 6;
    setSquares1to6(
      "perspective(500px) rotateY(" +
        posX * 0.05 +
        "deg) rotateX(" +
        posY * -0.05 +
        "deg)"
    );
    setSquares7and8(
      "perspective(500px) rotateY(" +
        posX * 0.02 +
        "deg) rotateX(" +
        posY * -0.02 +
        "deg)"
    );
  };

  return (
    <div className="wrapper">
      <div className="page-header">
        <div className="page-header-image" />
        <div className="content">
          <Container>
            <Row>
              <Col className="offset-lg-0 offset-md-3" lg="5" md="6">
                {" "}
                <div
                  className="square square-7"
                  id="square7"
                  style={{ transform: squares7and8 }}
                />
                <div
                  className="square square-8"
                  id="square8"
                  style={{ transform: squares7and8 }}
                />
                <Card className="card-register">
                  <CardHeader>
                    <CardImg
                      alt="..."
                      src={require("assets/img/square-purple-1.png").default}
                    />
                    <CardTitle className="" tag="h4">
                      Login
                    </CardTitle>
                  </CardHeader>
                  <CardBody>
                    <form>
                      <FormGroup label="Username">
                        <Input
                          value={username}
                          placeholder={placeholder.username}
                          onChange={(username) =>
                            setState((ss) => ({ ...ss, username }))
                          }
                        />
                      </FormGroup>

                      <FormGroup label="Email">
                        <Input
                          type="email"
                          placeholder={placeholder.email}
                          value={email}
                          onChange={(email) =>
                            setState((ss) => ({ ...ss, email }))
                          }
                        />
                      </FormGroup>

                      <FormGroup label="Password">
                        <Input
                          type="password"
                          placeholder={placeholder.password}
                          value={password}
                          onChange={(password) =>
                            setState((ss) => ({ ...ss, password }))
                          }
                        />
                      </FormGroup>

                      <FormGroup>
                        <AuthContext.Consumer>
                          {({ setAuthStatus }) => (
                            <Button
                              className="btn-round"
                              color="primary"
                              type="button"
                              onClick={(e) =>
                                props.handleRegister(e, setAuthStatus)
                              }
                            >
                              Register
                            </Button>
                          )}
                        </AuthContext.Consumer>
                      </FormGroup>
                    </form>
                  </CardBody>
                </Card>
              </Col>
              <Link to="/register">
                {" "}
                <Button
                  className="btn-simple btn-round"
                  color="primary"
                  type="button"
                  size="lg"
                >
                  Register
                </Button>
              </Link>
            </Row>
          </Container>
        </div>
      </div>

      <div className="auth__bg" />
    </div>
  );
};

export const Register = compose(
  withRegister,
  withState(initialState),
  withHandlers({
    handleRegister: (props) => async (e, setAuthStatus) => {
      e.preventDefault();
      const { setState, state } = props;
      const { username, email, password } = state;

      if (state.isSubmitting) {
        return;
      }

      try {
        setState((ss) => ({ ...ss, isSubmitting: true }));
        const response = await props.registerUser({
          username,
          email,
          password,
        });
        const token = response.data.register;

        storeAuthToken(token);
        setAuthStatus(true);
        props.history.push("/home");
        console.log("token", token);
      } catch (err) {
        setState((ss) => ({ ...ss, isSubmitting: false }));
        console.log(`handleRegister: ${err}`);
      }
    },
  })
)(RegisterComponent);
