import React from "react";
import { withRouter, Link } from "react-router-dom";
import { compose, withHandlers } from "recompose";
import { Dropdown } from "../../../shared/components/Dropdown";
// import { DropdownMenu } from "../../../shared/components/DropdownMenu";
import Icon from "react-ionicons";
import { withState } from "../../../shared/containers/withState";
import { DropdownMenuNew } from "../../../shared/components/DropdownMenuNew";

import {
  removeAuthToken,
  withUserId,
} from "../../../shared/utils/localStorageUtil";
import { withUserName } from "../../../api/user/withUserName";
// import "../../../styles/layout/UserMenu.css";
import {
  Button,
  Collapse,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  Row,
  Col,
  UncontrolledTooltip,
} from "reactstrap";

const initialState = {
  isMenuOpen: false,
};

const UserMenuComponent = (props) => {
  const { username, menuPrefixCls, state } = props;
  const menuClass = state.isMenuOpen
    ? `${menuPrefixCls} ${menuPrefixCls}--open`
    : menuPrefixCls;

  const [collapseOpen, setCollapseOpen] = React.useState(false);
  const [collapseOut, setCollapseOut] = React.useState("");
  const [color, setColor] = React.useState("navbar-transparent");

  const toggleCollapse = () => {
    document.documentElement.classList.toggle("nav-open");
    setCollapseOpen(!collapseOpen);
  };
  const onCollapseExiting = () => {
    setCollapseOut("collapsing-out");
  };
  const onCollapseExited = () => {
    setCollapseOut("");
  };

  const overlay = (
    <DropdownMenuNew
      menuItems={["profile", "logout"]}
      onClick={(menuItem) => props.handleMenuItemClick(menuItem)}
    />
  );

  return (
    <Navbar
      className="sticky bg-info navbar navbar-expand-lg"
      color-on-scroll="100"
      expand="lg"
    >
      <Container>
        <div className="navbar-translate">
          <NavbarBrand to="/" id="navbar-brand" tag={Link}>
            <span className="middle-align brand-text">{username}</span> • one of
            Will Powered Students finest
          </NavbarBrand>
        </div>
        <Collapse
          className={"justify-content-end " + collapseOut}
          navbar
          isOpen={collapseOpen}
          onExiting={onCollapseExiting}
          onExited={onCollapseExited}
        >
          <div className="navbar-collapse-header">
            <Row>
              <Col className="collapse-close text-right" xs="6">
                <button
                  aria-expanded={collapseOpen}
                  className="navbar-toggler"
                  onClick={toggleCollapse}
                >
                  <i className="tim-icons icon-simple-remove" />
                </button>
              </Col>
            </Row>
          </div>
          <Nav navbar>
            <UncontrolledDropdown nav>
              <Dropdown overlay={overlay}>
                <Container className={menuClass}></Container>
              </Dropdown>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
      </Container>
    </Navbar>
  );
};

UserMenuComponent.defaultProps = {
  menuPrefixCls: "user-menu",
};

export const UserMenu = compose(
  withRouter,
  withUserId,
  withUserName,
  withState(initialState),
  withHandlers({
    handleMenuItemClick: (props) => (menuItem) => {
      if (menuItem === "profile") {
      }

      if (menuItem === "logout") {
        removeAuthToken();
        props.history.push("/");
      }
    },
  })
)(UserMenuComponent);
