import React from "react";
import PropTypes from "prop-types";
import { compose } from "recompose";
import { Tooltip } from "../../../shared/components/Tooltip";
import { Icon } from "../../../shared/components/Icon";
import { TaskForm } from "./TaskForm";
import { withState } from "../../../shared/containers/withState";
import "../../../styles/routes/AddTaskButton.css";

import {
  Card as CardThing,
  Button,
  CardHeader,
  Table,
  Navbar,
  Container,
  CardBody,
} from "reactstrap";

const initialState = {
  isTaskFormVisible: false,
};

const AddTaskButtonComponent = (props) => {
  const { columnId, state, setState } = props;

  return (
    <React.Fragment>
      <CardBody className="content-center">
        <Button
          className="btn btn-sm btn-neutral content-center"
          onClick={() => setState((ss) => ({ ...ss, isTaskFormVisible: true }))}
        >
          Add Task
        </Button>
      </CardBody>

      {state.isTaskFormVisible && (
        <TaskForm
          columnId={columnId}
          dismiss={() =>
            setState((ss) => ({ ...ss, isTaskFormVisible: false }))
          }
        />
      )}
    </React.Fragment>
  );
};

AddTaskButtonComponent.propTypes = {
  columnId: PropTypes.number.isRequired,
};

export const AddTaskButton = compose(withState(initialState))(
  AddTaskButtonComponent
);
