import React from "react";
import PropTypes from "prop-types";
import { compose, withHandlers, withProps } from "recompose";

import { Card1 } from "../../../shared/components/Card";
import { ProjectForm } from "./ProjectForm";
import { EmptyList } from "../../../shared/components/EmptyList";
import { List } from "../../../shared/components/List";
import { ProjectListItem } from "./ProjectListItem";
import { withState } from "../../../shared/containers/withState";
import { emptyProjectCard } from "../../../shared/constants/homeConstants";
import { withProjectsByUserId } from "../../../api/project/withProjectsByUserId";

import classnames from "classnames";

import PerfectScrollbar from "perfect-scrollbar";

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  Label,
  FormGroup,
  Form,
  Input,
  FormText,
  NavItem,
  NavLink,
  Nav,
  Table,
  TabContent,
  TabPane,
  Container,
  Row,
  Col,
  ListGroup,
  CardTitle,
  ListGroupItem,
  CardSubtitle,
  UncontrolledTooltip,
  UncontrolledCarousel,
} from "reactstrap";

const initialState = {
  isAddProjectModalVisible: false,
  selectedProject: null,
};

const ProjectCardComponent = (props) => {
  const { projects, userId, state } = props;

  const extra = (
    <a style={{ fontSize: "12px" }} onClick={props.handleShowModal}>
      add project
    </a>
  );

  return (
    <Card className="card card-coin card-plain">
      <CardBody className="card-body">
        <Row>
          <Col className="text-center" md="12">
            <h4 className="brand-text title">Projects</h4>
            <Button onClick={props.handleShowModal}>create project</Button>
            <hr className="line-primary" />
          </Col>
        </Row>
        {!projects.length && (
          <EmptyList
            title={emptyProjectCard.title}
            description={emptyProjectCard.description}
          />
        )}
        <Table className="tablesorter" responsive>
          {!!projects.length && (
            <tbody>
              {projects.map((project) => (
                <ProjectListItem
                  key={project.id}
                  project={project}
                  editProject={(project) =>
                    props.handleShowModalWithProject(project)
                  }
                />
              ))}
            </tbody>
          )}
        </Table>

        {state.isAddProjectModalVisible && (
          <ProjectForm
            dismiss={props.handleHideModal}
            userId={userId}
            project={state.selectedProject}
          />
        )}
      </CardBody>
    </Card>
  );
};

ProjectCardComponent.propTypes = {
  userId: PropTypes.number.isRequired,
};

export const ProjectCard = compose(
  withProjectsByUserId,
  withState(initialState),
  withProps((props) => {
    const { projectsData } = props;
    const projects = (projectsData && projectsData.getProjectsByUserId) || [];

    return {
      projects,
    };
  }),
  withHandlers({
    handleShowModal: (props) => () =>
      props.setState((ss) => ({ ...ss, isAddProjectModalVisible: true })),
    handleShowModalWithProject: (props) => (project) =>
      props.setState((ss) => ({
        ...ss,
        selectedProject: project,
        isAddProjectModalVisible: true,
      })),
    handleHideModal: (props) => () =>
      props.setState((ss) => ({
        ...ss,
        selectedProject: null,
        isAddProjectModalVisible: false,
      })),
  })
)(ProjectCardComponent);
