import React from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import { compose } from "recompose";
import { Card } from "../../../shared/components/Card";
// import "../../../styles/routes/TeamCard.css";
import { CardHeader, Table, CardBody } from "reactstrap";

const TeamCardComponent = (props) => {
  const { title, subTitle, linkUrl, children } = props;

  return (
    <div>
      <Card>
        <CardBody className="">
          <a onClick={() => props.history.push(`/project/${linkUrl}`)}>
            {title}
          </a>

          <h4 className="info-title">{subTitle}</h4>
          <div>{children}</div>
        </CardBody>
      </Card>
      <hr className="line-primary" />
    </div>
  );
};

TeamCardComponent.propTypes = {
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string.isRequired,
  linkUrl: PropTypes.string.isRequired,
  children: PropTypes.node,
};

export const TeamCard = compose(withRouter)(TeamCardComponent);
