import React from "react";
import PropTypes from "prop-types";
import { compose, withHandlers, withProps } from "recompose";
import { Card1 } from "../../../shared/components/Card";
import { EmptyList } from "../../../shared/components/EmptyList";
import { TeamForm } from "./TeamForm";
import { TeamListItem } from "./TeamListItem";
import { TeamRowItem } from "./TeamRowItem";
import { List } from "../../../shared/components/List";
import { withState } from "../../../shared/containers/withState";
import { emptyTeamList } from "../../../shared/constants/homeConstants";
import { withTeamsByUserId } from "../../../api/team/withTeamsByUserId";

import classnames from "classnames";

import PerfectScrollbar from "perfect-scrollbar";

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  Label,
  FormGroup,
  Form,
  Input,
  FormText,
  NavItem,
  NavLink,
  Nav,
  Table,
  TabContent,
  TabPane,
  Container,
  Row,
  Col,
  ListGroup,
  CardTitle,
  ListGroupItem,
  CardSubtitle,
  UncontrolledTooltip,
  UncontrolledCarousel,
} from "reactstrap";

const initialState = {
  isTeamFormVisible: false,
  selectedTeam: null,
};

let ps = null;

const TeamCardComponent = (props) => {
  const { teams, userId, state } = props;

  const extra = (
    <a style={{ fontSize: "12px" }} onClick={props.handleShowModal}>
      add team
    </a>
  );

  const [tabs, setTabs] = React.useState(1);

  return (
    <Card className="card card-coin card-plain">
      <CardBody className="card-body">
        <Row>
          <Col className="text-center" md="12">
            <h4 className="brand-text title">Teams</h4>
            <Button onClick={props.handleShowModal}>add team</Button>
            <hr className="line-primary" />
          </Col>
        </Row>
        {!teams.length && (
          <EmptyList
            title={emptyTeamList.title}
            description={emptyTeamList.description}
          />
        )}
        <Table className="tablesorter" responsive>
          {!!teams.length && (
            <tbody>
              {teams.map((team) => (
                <TeamRowItem
                  key={team.id}
                  team={team}
                  editTeam={(team) => props.handleShowModalWithTeam(team)}
                />
              ))}
            </tbody>
          )}
        </Table>

        {state.isTeamFormVisible && (
          <TeamForm
            dismiss={props.handleHideModal}
            userId={userId}
            team={state.selectedTeam}
          />
        )}
      </CardBody>
    </Card>
  );
};

TeamCardComponent.propTypes = {
  userId: PropTypes.number.isRequired,
};

export const TeamCard = compose(
  withTeamsByUserId,
  withState(initialState),
  withProps((props) => {
    const { teamsData } = props;
    const teams = (teamsData && teamsData.getTeamsByUserId) || [];

    return {
      teams,
    };
  }),
  withHandlers({
    handleShowModal: (props) => () =>
      props.setState((ss) => ({ ...ss, isTeamFormVisible: true })),
    handleShowModalWithTeam: (props) => (team) =>
      props.setState((ss) => ({
        ...ss,
        selectedTeam: team,
        isTeamFormVisible: true,
      })),
    handleHideModal: (props) => () =>
      props.setState((ss) => ({
        ...ss,
        selectedTeam: null,
        isTeamFormVisible: false,
      })),
  })
)(TeamCardComponent);
