import React from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import { compose } from "recompose";
import { Icon } from "./Icon";
import "../../styles/components/PageHeader.css";
import { Button, Col, Row } from "reactstrap";

export const PageHeaderComponent = (props) => {
  const { title, icon, subTitle, children, hasBackBtn } = props;
  return (
    <div className="page-header">
      <div className="page-header__left">
        <Row>
          <Col>
            {hasBackBtn && (
              <Button
                className="btn-icon btn-round btn btn-neutral btn-default"
                onClick={() => props.history.goBack()}
              >
                {icon}
              </Button>
            )}
          </Col>
          <Col>
            <h1 className="mb-0 brand-text text-white">{title}</h1>
            <span className="page-header__sub-title">{subTitle}</span>
          </Col>
        </Row>
      </div>
      {children && <div className="page-header__extra">{children}</div>}
    </div>
  );
};

PageHeaderComponent.propTypes = {
  title: PropTypes.node.isRequired,
  subTitle: PropTypes.node,
  children: PropTypes.node,
  hasBackBtn: PropTypes.bool,
};

export const PageHeader = compose(withRouter)(PageHeaderComponent);
