import React from "react";
import PropTypes from "prop-types";
// import '../../styles/components/DropdownMenu.css';

import {
  Collapse,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  Row,
  Col,
} from "reactstrap";

export const DropdownMenuNew = (props) => {
  const { menuItems, onClick } = props;

  return (
    <Collapse navbar isOpen={true}>
      <Nav className="ml-auto" navbar>
        <UncontrolledDropdown nav>
          <DropdownToggle
            aria-expanded={false}
            aria-haspopup={true}
            caret
            color="default"
            data-toggle="dropdown"
            href="http://example.com"
            id="navbarDropdownMenuLink"
            nav
          >
            {" "}
            <i aria-hidden={true} className="tim-icons icon-settings-gear-63" />
          </DropdownToggle>

          <DropdownMenu aria-labelledby="navbarDropdownMenuLink">
            {menuItems.map((menuItem, i) => (
              <DropdownItem key={i} onClick={() => onClick(menuItem)}>
                {menuItem}
              </DropdownItem>
            ))}
          </DropdownMenu>
        </UncontrolledDropdown>
      </Nav>
    </Collapse>
  );
};

DropdownMenuNew.propTypes = {
  menuItems: PropTypes.arrayOf(PropTypes.string).isRequired,
  onClick: PropTypes.func,
};
