import React from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import { compose, withHandlers } from "recompose";
import { ListItem } from "../../../shared/components/ListItem";

import { TeamRow } from "../../../shared/components/TeamRow";
import { Dropdown } from "../../../shared/components/Dropdown";
import { DropdownMenuNew } from "../../../shared/components/DropdownMenuNew";
import Icon from "react-ionicons";
import { withDeleteTeam } from "../../../api/team/withDeleteTeam";

import { Container, NavbarBrand } from "reactstrap";

const TeamListItemComponent = (props) => {
  const { team } = props;

  //   const overlay = (
  //     <DropdownMenuNew
  //       menuItems={["edit", "delete"]}
  //       onClick={(menuItem) => props.handleDropdownClick(menuItem)}
  //     />
  //   );

  const dropdown = (
    <DropdownMenuNew
      menuItems={["edit", "delete"]}
      onClick={(menuItem) => props.handleDropdownClick(menuItem)}
    />
  );

  return (
    <TeamRow
      title={
        <NavbarBrand onClick={() => props.history.push(`/team/${team.id}`)}>
          {team.name}
        </NavbarBrand>
      }
      description={team.description}
      extra={dropdown}
    />
  );
};

TeamListItemComponent.propTypes = {
  team: PropTypes.object.isRequired,
  editTeam: PropTypes.func.isRequired,
};

export const TeamRowItem = compose(
  withRouter,
  withDeleteTeam,
  withHandlers({
    handleDropdownClick: (props) => async (menuItem) => {
      const { team, deleteTeam, editTeam } = props;
      if (menuItem === "delete") {
        try {
          await deleteTeam({ teamId: team.id });
        } catch (err) {
          console.log(`TeamListItem: handle delete ${err}`);
        }
      }

      if (menuItem === "edit") {
        editTeam(team);
      }
    },
  })
)(TeamListItemComponent);
