import React from "react";
import PropTypes from "prop-types";
import { compose, withProps, branch, renderComponent } from "recompose";
import { Column } from "./components/Column";
import { PageHeader } from "../../shared/components/PageHeader";
import { Button } from "../../shared/components/Button";
import { AddColumnButton } from "./components/AddColumnButton";
import { withProjectById } from "../../api/project/withProjectById";
// import "../../styles/routes/Project.css";
import classnames from "classnames";
// javascript plugin used to create scrollbars on windows
import PerfectScrollbar from "perfect-scrollbar";
import {
  // Button,
  Card,
  CardHeader,
  CardBody,
  Label,
  FormGroup,
  Form,
  Input,
  FormText,
  NavItem,
  NavLink,
  Nav,
  Table,
  TabContent,
  TabPane,
  Container,
  Row,
  Col,
  UncontrolledTooltip,
  UncontrolledCarousel,
} from "reactstrap";

let ps = null;

const ProjectComponent = (props) => {
  const { project, columns, projectId, columnOrderMax } = props;

  React.useEffect(() => {
    if (navigator.platform.indexOf("Win") > -1) {
      document.documentElement.className += " perfect-scrollbar-on";
      document.documentElement.classList.remove("perfect-scrollbar-off");
      let tables = document.querySelectorAll(".table-responsive");
      for (let i = 0; i < tables.length; i++) {
        ps = new PerfectScrollbar(tables[i]);
      }
    }
    document.body.classList.toggle("project-page");
    // Specify how to clean up after this effect:
    return function cleanup() {
      if (navigator.platform.indexOf("Win") > -1) {
        ps.destroy();
        document.documentElement.className += " perfect-scrollbar-off";
        document.documentElement.classList.remove("perfect-scrollbar-on");
      }
      document.body.classList.toggle("project-page");
    };
  }, []);

  return (
    <div className="wrapper">
      <section className="section section-lg">
        <div className="page-header">
          <Container>
            <Row>
              <PageHeader
                title={project.title.toUpperCase()}
                hasBackBtn={true}
                subTitle={`Manage the ${project.title} project here`}
                icon={
                  <i
                    aria-hidden={true}
                    className="tim-icons icon-double-left"
                    onClick={() => props.history.goBack()}
                  />
                }
              ></PageHeader>
            </Row>
          </Container>
          <img
            alt="..."
            className="dots"
            src={require("assets/img/dots.png").default}
          />
          <img
            alt="..."
            className="path"
            src={require("assets/img/path4.png").default}
          />
        </div>
        <section className="section-story-overview">
          <div className=" project-grid">
            <div className="project__column-grid">
              {columns.map((column) => (
                <Column key={column.id} column={column} projectId={projectId} />
              ))}

              <div className="project-column">
                <AddColumnButton
                  projectId={projectId}
                  columnsLength={columnOrderMax}
                />
              </div>
            </div>
          </div>
        </section>
      </section>
    </div>
  );
};

ProjectComponent.propTypes = {
  projectId: PropTypes.number,
  columns: PropTypes.arrayOf(PropTypes.object),
};

export const Project = compose(
  withProps((props) => {
    const { projectId } = props.match.params;

    return {
      projectId: parseInt(projectId, 10),
    };
  }),
  withProjectById,
  withProps((props) => {
    const { data } = props;
    const project = data && data.getProjectById;
    let columns =
      (data &&
        data.getProjectById &&
        data.getProjectById.columns && [...data.getProjectById.columns]) ||
      [];
    const columnOrderMax =
      columns &&
      Math.max.apply(
        null,
        columns.map((column) => column.order)
      );

    columns.sort((a, b) => a.order - b.order);

    return { project, columns, columnOrderMax };
  }),
  branch(
    (props) => props.data.loading,
    renderComponent(() => <span>Loading...</span>)
  )
)(ProjectComponent);
