import React from "react";
import PropTypes from "prop-types";
import { Container, NavbarBrand, Navbar } from "reactstrap";
// import "../../styles/components/ListItem.css";

export const ProjectRow = (props) => {
  const { avatarUrl, title, description, children, extra } = props;
  return (
    <tr className="fixed-row">
      <td className="fixed-row">
        <Navbar className="p-0 navbar-transparent" expand="lg">
          <Container className="p-0">
            {title}
            {extra}
          </Container>
        </Navbar>

        {avatarUrl && <div className=""></div>}
        {/* {extra && <span className="">{extra}</span>}
        {title && <h4 className="">{title}</h4>} */}
        {description && <span className="">{description}</span>}
        {children && <div className="">{children}</div>}
      </td>
    </tr>
  );
};

ProjectRow.propTypes = {
  avatarUrl: PropTypes.string,
  title: PropTypes.node,
  description: PropTypes.node,
  children: PropTypes.node,
  extra: PropTypes.node,
};
